import React, {Component} from 'react';
import scalaData from './scaladata';

class PastScalas extends Component {
   constructor() {
      super();
      this.state = {};
   }
   render() {
      let scalas = [];
      for (var a = 0; a < scalaData.length; a++) {
      scalas.push(
         <figure key={scalaData[a].key}>
         <a href={scalaData[a].link}><img className='sideImg' src={scalaData[a].imgSrc} alt='' /></a>
         <figcaption>{scalaData[a].title}</figcaption>
         </figure>
      );
      }
      return (<div id='drawer' style={{paddingBottom : 60}}>{scalas}</div>);
   }
}

export default PastScalas;
