import React, {Component} from 'react';
import data from '../app_data.js';
import StepCard from './StepCard';
import Reflect from './Reflect';
import InteractiveSVG from './InteractiveSVG';
import $ from 'jquery';
import interact from 'interactjs';

class ImageInteractive extends Component {
   constructor() {
      super();
      this.state = {
      currentButton : 1, mode : 'learn', currentAnswerMark : 1, mediaDimTag : '',
      testKey : 1, audioVol : 1
      };
      this.childClick = this.childClick.bind(this);
      this.startOver = this.startOver.bind(this);
      this.reset = this.reset.bind(this);
      this.upOne = this.upOne.bind(this);
      this.testMode = this.testMode.bind(this);
      this.learnMode = this.learnMode.bind(this);
      this.correctAnswer = this.correctAnswer.bind(this);
      this.updateTest = this.updateTest.bind(this);
      this.setAudio = this.setAudio.bind(this);
      this.repeatStep = this.repeatStep.bind(this);
   }
   updateMedia() {
     let wdt = $('#media-container').width();
     let hgt = $('#media-container').height();
     let mediatag;
     if (wdt / hgt <= 1.2) mediatag = '_square';
     else mediatag = '';
     this.setState({mediaDimTag : mediatag});
     if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
     else this.setState({mediaDims : {height : '54vh'}});
   }
   componentDidMount() {
      this.updateMedia();
      let wdt = $('#media-container').width();
      let hgt = $('#media-container').height();
      if (wdt / hgt > 1.72) this.setState({mediaDims : {height : '65vh'}});
      window.addEventListener('resize', this.updateMedia.bind(this));
   }
   childClick() {
      this.setState({currentButton : this.state.currentButton + 1}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      });
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${data.audio_abbrev}${this.state.currentButton}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
      }
   }
   correctAnswer(fin) {
      this.setState({currentAnswerMark : this.state.currentAnswerMark + 1}, function() {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      if (!fin) {
         const cardLoc = document.getElementById(`card${this.state.currentAnswerMark}`).offsetTop;
         setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 800);
      }
      });
   }
   startOver() {
      this.setState({currentButton : 2}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         let audio = document.getElementById(`${data.audio_abbrev}${this.state.currentButton - 1}`);
         if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
         }
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   reset() {
      this.setState({currentButton : 1}, function() {
         let sounds = document.getElementsByTagName('audio');
         for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
         }
      );
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      $('#reflectQuestion').val('');
   }
   upOne(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${data.audio_abbrev}${this.state.currentButton - 2}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
      this.setState({currentButton : this.state.currentButton - 1}, function() {
      if (this.state.currentButton > 1) {
      const stepWindow = document.getElementById('steps-container');
      const stepWindowLoc = document.getElementById('steps-container').offsetTop;
      const cardLoc = document.getElementById(`card${this.state.currentButton - 1}`).offsetTop;
      setTimeout(function(){stepWindow.scroll({top: cardLoc - stepWindowLoc, behavior: 'smooth' });}, 100);
      }
      });
   }
   repeatStep(evt) {
      let sounds = document.getElementsByTagName('audio');
      for (let i = 0; i < sounds.length; i++) {sounds[i].pause(); sounds[i].currentTime = 0}
      let audio = document.getElementById(`${data.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) {
         audio.volume = this.state.audioVol;
         audio.play();
      }
      evt.stopPropagation();
   }
   setAudio() {
      let level = this.state.audioVol;
      if (level === 0) level = 1;
      else level = 0;
      this.setState({audioVol : level});
      let audio = document.getElementById(`${data.audio_abbrev}${this.state.currentButton - 1}`);
      if (audio) audio.volume = level;
   }
   testMode() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      interact('#dragBut1').unset();
      interact('#dragBut2').unset();
      let sound = document.getElementById(`${data.audio_abbrev}${this.state.currentButton - 1}`);
      if (sound) {sound.pause(); sound.currentTime = 0;}
   }
   updateTest() {
      this.setState({currentButton : 2, mode : 'test', currentAnswerMark : 1,
                     testKey : this.state.testKey + 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      interact('#dragBut1').unset();
      interact('#dragBut2').unset();
   }
   learnMode() {
      this.setState({currentButton : 1, mode : 'learn', currentAnswerMark : 1});
      setTimeout(function(){window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);}, 50);
      const stepWindow = document.getElementById('steps-container');
      stepWindow.scroll({top: 0, behavior: 'smooth' });
      interact('#dragBut1').unset();
      interact('#dragBut2').unset();
   }
   render() {
      let modeMedia, butStyle, buttonChoices, mediaStyle, testText, reflect, audvis, repeat;
      let stepCards = [];
      mediaStyle = this.state.mediaDims;

      if (this.state.currentButton <= data.steps.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.mode === 'learn') {
      for (let a = 0; a < data.steps.length; a++) {
         stepCards.push(<StepCard key={data.steps[a].id} label={data.steps[a].label} text={data.steps[a].text} order={data.steps[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} upone={this.upOne} repstep={this.repeatStep} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} />)
      }

      buttonChoices = (
         <span>
         <button onClick={this.startOver} className='uk-button uk-button-default uk-button-small uk-width-1-3' style={butStyle}>redo</button>

         <button style={{backgroundColor : this.props.seccolor}} onClick={this.reset} className='uk-button uk-button-secondary uk-button-small uk-width-1-3'>clear</button>

         <button style={{backgroundColor : this.props.primcolor}} onClick={this.testMode} className='uk-button uk-button-primary uk-button-small uk-width-1-3'>show</button>
         </span>
      );
      modeMedia = <img src={this.state.currentButton !== 1 ? `${data.img_abbrev}${String(this.state.currentButton - 1)}${this.state.mediaDimTag}.svg` : ''} alt='' />;
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
         audvis = 'hidden';
      }
      else audvis = 'visible';
      reflect = <Reflect suganswer={data.reflect_suganswer} reflectQ={data.reflectQ} />;
      }
      else  {
      if (this.state.currentButton <= data.steps.length) butStyle = {pointerEvents : 'none', backgroundColor : this.props.discolor};
      else butStyle = {pointerEvents : 'all', backgroundColor : 'white'};

      if (this.state.testKey < 3) repeat = 'another one';
      else repeat = 'again';

      buttonChoices = (
         <span>
         <button style={{backgroundColor : this.props.seccolor}} onClick={this.updateTest} className='uk-button uk-button-secondary uk-button-small uk-width-1-2'>{repeat}</button>

         <button style={{backgroundColor : this.props.primcolor}} onClick={this.learnMode} className='uk-button uk-button-primary uk-button-small uk-width-1-2'>learn&thinsp;<span uk-icon='social'></span></button>
         </span>
      );

      for (let a = 0; a < data.steps.length; a++) {
         if (this.state.testKey % 3 === 1) testText = data.steps[a].test;
         else if (this.state.testKey % 3 === 2) testText = data.steps[a].test2;
         else if (this.state.testKey % 3 === 0) testText = data.steps[a].test3;
         stepCards.push(<StepCard key={data.steps[a].id} label={data.steps[a].testLabel} text={testText} order={data.steps[a].order} handler={this.childClick} curbut={this.state.currentButton} curmode={this.state.mode} curmark={this.state.currentAnswerMark} primcolor={this.props.primcolor} seccolor={this.props.seccolor} discolor={this.props.discolor} dislabel={this.props.dislabel} />)
      }

      modeMedia = <InteractiveSVG testnum={this.state.testKey} curbut={this.state.currentButton} correctans={this.correctAnswer} curans={this.state.currentAnswerMark} />;

      reflect = null;
      audvis = 'hidden';
      }
      return (
      <div>
      <div id='media-container' className='uk-container uk-width-1-2@m uk-border-rounded' style={mediaStyle}>
      {modeMedia}
      <img onClick={this.setAudio} id='soundicon' src='audio_icon.svg' alt='' style={{opacity : ((1 - this.state.audioVol) * 1.5) / 1.5 + 0.2, visibility : audvis}}/>
      </div>

      <div id='steps-container' className='uk-container uk-width-1-2@m'>
      {stepCards}
      {reflect}
      {buttonChoices}
      </div>
      </div>
      );
   }
}

export default ImageInteractive;
