import React, {Component} from 'react';
import * as MyScriptJS from 'myscript'
import 'myscript/dist/myscript.min.css';
import $ from 'jquery';

const editorStyle = {width : '100%', height : '100%', touchAction : 'none'};

const numerStyle = {width : '7vh', height : '7vh', fontFamily : 'sans-serif', fontSize : '1.4em', textAlign : 'center', outline : 'none', boxShadow : '0 0 3px #ccc', borderBottom : '1px solid gray', overflow : 'hidden', display : 'flex', alignItems : 'center', justifyContent : 'center', backgroundColor : '#fafafa', color : 'black'};

const denomStyle = {width : '7vh', height : '7vh', fontFamily : 'sans-serif', fontSize : '1.4em', textAlign : 'center', outline : 'none', boxShadow : '0 0 3px #ccc', borderTop : '1px solid gray', overflow : 'hidden', display : 'flex', alignItems : 'center', justifyContent : 'center', backgroundColor : '#fafafa', color : 'black'};

const inOutLabelStyleLeft = {position : 'absolute', bottom : 0, right : 0, backgroundColor : 'rgba(21, 160, 133, 0.5)', color : 'white', fontFamily : 'Rubik', fontSize : '0.9em', fontVariant : 'small-caps', borderTopLeftRadius : '4px', width : '75px', height : '25px', display : 'flex', alignItems : 'center', justifyContent : 'center'};

const inOutLabelStyleRight = {position : 'absolute', top : 0, right : 0, backgroundColor : 'rgba(21, 160, 133, 0.5)', color : 'white', fontFamily : 'Rubik', fontSize : '0.9em', fontVariant : 'small-caps', borderBottomLeftRadius : '4px', width : '75px', height : '25px', display : 'flex', alignItems : 'center', justifyContent : 'center'};

const clearStyle = {position : 'absolute', bottom : '0px', right : '0px', height : '25px', boxShadow : '0 0 2px #ccc', outline : 'none', fontVariant : 'small-caps', backgroundColor : '#fafafa', color : 'gray', border : 'none', width : '75px', borderTopLeftRadius : '4px', cursor : 'pointer', zIndex : '100'};

class InteractiveSVG extends Component {
   constructor() {
      super();
      this.state = {};
      this.handleAnswers = this.handleAnswers.bind(this);
      this.clearAnswer = this.clearAnswer.bind(this);
   }
  componentDidMount() {
    this.editor = MyScriptJS.register(this.refs.editor, {
    recognitionParams: {
    type: 'MATH',
    protocol: 'WEBSOCKET',
    apiVersion: 'V4',
    server: {
    scheme: 'https',
    host: 'webdemoapi.myscript.com',
    applicationKey: '1463c06b-251c-47b8-ad0b-ba05b9a3bd01',
    hmacKey: '60ca101a-5e6d-4159-abc5-2efcbecce059',
    },
    },
    });

    window.addEventListener("resize", () => {this.editor.resize()});
    let inp = document.getElementById('input-area');

    inp.addEventListener('exported', (evt) => {
    var exports = evt.detail.exports || {};
    var answer = exports['application/x-latex'] || '';
    this.handleAnswers(answer);
    });
    window.addEventListener('touchmove', function() {});
  }
  handleAnswers(ans) {
     if (ans === '') return;
     if (this.props.testnum % 3 === 1) {

        if (this.props.curans === 1) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '1') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 2) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '2') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 3) {
        if (ans === '2') {
        this.props.correctans();
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
        else if (this.props.curans === 4) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '4') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 5) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '8') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 6) {
        if (ans === '2') {
        this.props.correctans(true);
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
     }
     else if (this.props.testnum % 3 === 2) {

        if (this.props.curans === 1) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '1') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 2) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '4') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 3) {
        if (ans === '4') {
        this.props.correctans();
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
        else if (this.props.curans === 4) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '5') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 5) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '20') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 6) {
        if (ans === '4') {
        this.props.correctans(true);
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
     }
     else if (this.props.testnum % 3 === 0) {

        if (this.props.curans === 1) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '1') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 2) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '2') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 3) {
        if (ans === '2') {
        this.props.correctans();
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
        else if (this.props.curans === 4) {
        $('#denomInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '2') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 5) {
        $('#numerInput').html(`\\(\\mathtt{${ans}}\\)`);
        if (ans === '4') {
        this.props.correctans();
        this.editor.clear();
        }
        }
        else if (this.props.curans === 6) {
        if (ans === '2') {
        this.props.correctans(true);
        this.editor.clear();
        $('#numerInput').html('');
        $('#denomInput').html('');
        }
        }
     }
     window.MathJax.Hub.Queue(['Typeset', window.MathJax.Hub]);
  }
  clearAnswer() {
     this.editor.clear();
     if (this.props.curans === 1 || this.props.curans === 4) $('#denomInput').html('');
     else if (this.props.curans === 2 || this.props.curans === 5) $('#numerInput').html('');
  }
  render() {
    let imgSrc, imgNum;
    if (this.props.curans >= 3 && this.props.curans < 6) imgSrc = this.props.curans - 1;
    else if (this.props.curans === 6) imgSrc = this.props.curans - 2;
    else if (this.props.curans > 6) imgSrc = 4;
    else imgSrc = this.props.curans;

    if (this.props.testnum % 3 === 1) imgNum = 1;
    else if (this.props.testnum % 3 === 2) imgNum = 2;
    else if (this.props.testnum % 3 === 0) imgNum = 3;

    return (
    <div id='interactive' uk-grid='true' className='uk-grid-collapse'>
    <div id='imgs' className='uk-width-1-2' style={{borderRight : '1px solid lightgray', position : 'relative'}}>
    <div style={inOutLabelStyleLeft}>model</div>
    <img src={`sbs_wntr_test${imgNum}_${imgSrc}.svg`} alt='' style={{height : '85%'}} />
    </div>
    <div id='input-output' className='uk-width-1-2' style={{height : '100%', touchAction : 'none'}}>
    <div id='output' style={{borderBottom : '1px solid lightgray', height : '50%', position : 'relative', display : 'flex', alignItems : 'center', justifyContent : 'center', flexDirection : 'column'}}>
    <div style={inOutLabelStyleRight}>fraction</div>
    <div id='numerInput' style={numerStyle}></div>
    <div id='denomInput' style={denomStyle}></div>
    </div>
    <div id='input-area' style={{height : '50%', position : 'relative'}}>
    <div style={inOutLabelStyleRight}>write</div>
    <div style={editorStyle} ref="editor" touch-action="none"></div>
    <button id='clearHandwriting' onClick={this.clearAnswer} style={clearStyle}>clear</button>
    </div>
    </div>
    </div>
    );
  }
}

export default InteractiveSVG;
