import React, {Component} from 'react';
import ImageInteractive from './ImageInteractive';
import PastScalas from './PastScalas';

class App extends Component {
   constructor() {
      super();
      this.state = {};
      this.openNav = this.openNav.bind(this);
      this.closeNav = this.closeNav.bind(this);
   }
   openNav() {
      document.getElementById('sideDrawer').style.width = '250px';
      document.getElementById('sideDrawer').classList.remove('slideOutLeft');
      document.getElementById('sideDrawer').classList.add('slideInLeft');
   }
   closeNav() {
      document.getElementById('sideDrawer').classList.remove('slideInLeft');
      document.getElementById('sideDrawer').classList.add('slideOutLeft');
   }
   render() {
      let primary = '#4499cc';
      let secondary = '#557722';
      const disabled = '#D3D3D3';
      let dislabel = '#aaccdd';

      let scala;
      if (this.props.media === 'images-interactive') {
         scala = <ImageInteractive primcolor={primary} seccolor={secondary} discolor={disabled} dislabel={dislabel} />
      }
      return (
      <div>
      <span id='drawerToggle' onClick={this.openNav} style={{backgroundColor : disabled}}>»</span>
      <div id='sideDrawer' className='sidenav animated'>
      <span className='closebtn' onClick={this.closeNav}>&times;</span>
      <PastScalas />
      </div>
      {scala}
      </div>
      );
   }
}

export default App;
